.container {
    --color: #239ce2;
    min-width: 700px;
    max-width: 850px;
    margin: 0 auto;
    flex: 1.2;
    height: fit-content;
    min-height: 900px;
    box-shadow: 1px 1px 3px 2px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    gap: 30px;
    padding: 30px;
  }
  
  .header {
    display: flex;
    flex-direction: column;
  }
  
  .header .heading {
    font-size: 2.7rem;
    font-weight: 500;
    text-transform: capitalize;
  }
  
  .header .subHeading {
    color: var(--color);
    font-weight: 500;
    font-size: 1.1rem;
  }
  
  .header .links {
    margin-top: 15px;
    display: flex;
    gap: 30px;
    flex-wrap: wrap;
    row-gap: 10px;
  }
  
  .header .link {
    font-size: 0.875rem;
    display: flex;
    align-items: center;
    gap: 5px;
    cursor: pointer;
  }
  
  .header .link svg {
    color: var(--color);
    height: 16px;
    width: 16px;
  }
  
  .main {
    display: flex;
    gap: 30px;
  }
  
  .col1 {
    flex: 1.3;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  .col2 {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  .section .sectionTitle {
    font-size: 1.4rem;
    font-weight: bold;
    width: 100%;
    border-bottom: 2px solid #000;
  }
  
  .section .content {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 10px 0;
  }
  .section .content .item {
    border-bottom: 1px dotted lightgray;
    display: flex;
    flex-direction: column;
    gap: 4px;
    padding-bottom: 10px;
  }
  .section .content .item:last-child {
    border-bottom: none;
  }
  
  .content .title {
    font-weight: 500;
    font-size: 1rem;
    line-height: 1.3rem;
  }
  
  .content .subTitle {
    font-weight: bold;
    color: var(--color);
    font-size: 1rem;
  }
  
  .content .overview {
    font-size: 0.875rem;
  }
  
  .content .link {
    display: flex;
    gap: 5px;
    font-size: 0.75rem;
    cursor: pointer;
    color: var(--color);
  }
  .content .link svg {
    height: 14px;
    width: 14px;
    color: var(--color);
  }
  
  .content .date {
    display: flex;
    gap: 5px;
    align-items: center;
    font-size: 0.875rem;
  }
  
  .content .date svg {
    height: 14px;
    width: 14px;
  }
  
  .content .points,
  .numbered {
    padding-left: 18px;
    font-size: 0.875rem;
  }
  
  .numbered li {
    list-style-type: decimal;
  }
  
  .hidden {
    display: none;
  }
  
  @media only screen and (max-width: 1110px) {
    .container {
      min-width: 90%; /* Adjust width for smaller screens */
      max-width: none; /* Remove max-width to allow flexibility */
    }
  
    .header .heading {
      font-size: 2rem; /* Adjust font size */
    }
  
    .header .subHeading {
      font-size: 1rem; /* Adjust font size */
    }
  
    .header .links {
      gap: 20px; /* Adjust gap between links */
    }
  
    .header .link svg {
      height: 14px; /* Adjust icon size */
      width: 14px;
    }
  
    .main {
      flex-direction: column; /* Stack columns for smaller screens */
    }
  
    .col1,
    .col2 {
      flex: 1; /* Allow columns to take full width */
    }
  
    .section .sectionTitle {
      font-size: 1.2rem; /* Adjust font size */
    }
  
    .content .title {
      font-size: 0.9rem; /* Adjust font size */
    }
  
    .content .subTitle {
      font-size: 0.9rem; /* Adjust font size */
    }
  
    .content .overview {
      font-size: 0.8rem; /* Adjust font size */
    }
  
    .content .link {
      font-size: 0.7rem; /* Adjust font size */
    }
  
    .content .link svg {
      height: 12px; /* Adjust icon size */
      width: 12px;
    }
  
    .content .date {
      font-size: 0.8rem; /* Adjust font size */
    }
  
    .content .date svg {
      height: 12px; /* Adjust icon size */
      width: 12px;
    }
  
    .content .points,
    .numbered {
      padding-left: 14px; /* Adjust padding for lists */
      font-size: 0.8rem; /* Adjust font size */
    }
  }